.product-list {
    max-width: 700px;
    padding: 0 10px;
    margin-top: 50px;
}

.product-list article {
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-list article .product-img {
    height: 100px;
    width: 100px;
    background-image: url('https://images-americanas.b2w.io/produtos/01/00/offers/01/00/item/133274/7/133274700_1GG.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.product-list article .product-data {
    margin-left: 20px;
    width: 100%;
}

.product-list article .product-data strong {
    font-size: 15px;
    color: #333;
    font-weight: 700;
    line-height: 20px;
}

.product-list article .product-data p {
    font-size: 14px;
    font-weight: 500;
    color: #6b6b6b;
    line-height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
}

.product-list article .product-data p strong {
    font-size: 18px;
    color: #444;
    line-height: 20px;
    font-weight: 900;
}

.product-list article .product-data a {
    height: 35px;
    border-radius: 5px;
    border: 2px solid #da255f;
    background: none;
    /* margin-top: 5px; */
    color: #da255f;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-list article .product-type {
    width: 50px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.product-list article .product-type .type-icon {
    height: 30px;
    width: 100%;
    background-image: url(/static/media/coupon.466f9353.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.product-list article .product-type p {
    font-size: 12px;
    color: #999;
    line-height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
}

.navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    margin-top: -15px;
}

.navigation button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 10px;
    background-color: #da255f;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
}

.navigation p {
    color: rgb(78, 78, 78);
    font-size: 13px;
    font-weight: 500;
}

.loading-image {
    height: 100%;
    width: 120px;
    background-image: url(/static/media/wait.1d7626e6.gif);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.request-error {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.request-error h1 {
    font-size: 18px;
    margin: 5px auto;
}

.request-error p {
    font-size: 14px;
    padding: 0 5px;
    text-align: center;
    color: #888;
}

.request-error button {
    margin-top: 10px;
    padding: 15px 20px;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#845ec2), color-stop(#ff6791), to(#ffc75f));
    background-image: -webkit-linear-gradient(left bottom, #845ec2, #ff6791, #ffc75f);
    background-image: linear-gradient(to right top, #845ec2, #ff6791, #ffc75f);
    border: none;
    color: #fff;
    font-weight: 600;
    border-radius: 50px;
    cursor: pointer !important;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body,
html {
    font-family: 'Lato', sans-serif;
    background: #fff;
    color: #333;
    text-rendering: optimizeSpeed !important;
    text-rendering: geometricPrecision !important;
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;
}
.tabs-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.tab-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100px;
}

.tab-item .item-img {
    height: 40px;
    width: 40px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.tab-item p {
    font-size: 12px;
    color: #777;
}
.header-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
}

.header-container .logo {
    height: 100px;
    width: 100px;
    background-image: url(/static/media/logo.7330026c.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border: 1px solid #ccc;
    border-radius: 50%;
}

.header-container .name {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-container .name p {
    margin-top: 3px;
    font-size: 18px;
    color: #444;
    font-weight: 800;
    line-height: 20px;
}

.header-container .instagram-checked {
    height: 17px;
    width: 17px;
    background-image: url(/static/media/checked.03ab809d.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.header-container p {
    margin-top: 3px;
    font-size: 12px;
    color: rgb(117, 117, 117);
    font-weight: 900px;
    line-height: 15px;
}
